import { graphql } from "gatsby";
import { object, shape } from "prop-types";
import Img from "gatsby-image";
import React, { Component, Fragment } from "react";
import styled from "styled-components";

import { breakpoint, color } from "ui/settings";
import { Copy, Heading, Separator } from "ui/components";
import { setSpace } from "ui/mixins";

import Layout from "../Layout";
import SEOWrapper from "../SEOWrapper";

const ItemImage = styled.div`
  ${breakpoint.phone} {
    ${setSpace("phh")};
    margin-bottom: -3em;
  }
  ${breakpoint.tabletUp} {
    flex: 0 0 ${100 / 3}%;
  }
`;
const ItemText = styled.div`
  h2 span {
    ${setSpace("pvx")};
    background: ${color.white};
  }
  h3 {
    color: #00a3da;
    background: transparent;
  }
  flex: 0 0 ${(100 / 3) * 2}%;
  position: relative;
  z-index: 5;
`;

const Item = styled(Copy)`
  ${setSpace("pvl")};
  display: flex;
  flex-direction: column;
  &:not(:last-child) {
    ${setSpace("mbh")};
  }
  ${breakpoint.tabletUp} {
    align-items: center;
    flex-direction: ${({ odd }) => (odd ? `row` : `row-reverse`)};
    ${ItemText} {
      ${({ odd }) => (odd ? setSpace("pll") : setSpace("prl"))};
    }
  }
`;

export default class PLBiennale2018Works extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const { data, location } = this.props;
    return (
      <Fragment>
        <SEOWrapper title="Realizacje" location={location} />
        <Layout location={location}>
          <Heading>Realizacje</Heading>
          <Separator silent size="l" />
          <Item odd>
            <ItemImage>
              <Img
                fluid={data.jazni.edges[0].node.childImageSharp.fluid}
                alt="Modernizacja jaźni"
              />
            </ItemImage>
            <ItemText>
              <h2>
                <span>Modernizacja jaźni</span>
              </h2>
              <h3>
                Threedotstype × Oskar Zięta
                <br />
                ul. Żeromskiego 26–31 (łącznik)
              </h3>
              <p>
                <i>
                  Zażółć gęślą jaźń. Możemy to sobie jakoś wyobrazić, że nasza
                  jaźń, moja wewnętrzna istota, pod wpływem grania na gęślach,
                  poczuje się trochę zażółcona, ale czy tylko kolorem?
                </i>{" "}
                Tak profesor Bralczyk próbował rozwikłać sens tego krótkiego,
                naszpikowanego polskimi znakami diakry­tycznymi, zdania, na
                którym twórcy fontów prezentują wygląd krojów pisma
                dostosowanych do naszego języka.
              </p>
              <p>
                Typograf Marian Misiak (Threedotstype) i projektant procesu
                technologicznego, Oskar Zięta, nadmuchali wspólnie cały alfabet.
                Nasze jaźnie są tym zachwycone, dlatego w tym roku pozdrawiamy
                Gdynię, najbardziej polskim zdaniem wykonanym w technologii
                FiDU.
              </p>
            </ItemText>
          </Item>
          <Item>
            <ItemImage>
              <Img
                fluid={data.falowa.edges[0].node.childImageSharp.fluid}
                alt="Modernizacja falowa"
              />
            </ItemImage>
            <ItemText>
              <h2>
                <span>Modernizacja falowa</span>
              </h2>
              <h3>
                UVMW
                <br />
                ul. Zygmunta Augusta 2
              </h3>
              <p>
                Rozrzeźbiona gresem fasada imituje taflę wody. Warszawskie
                studio UVMW czerpie w tym projekcie inspirację z polskich mozaik
                i abstrakcyjnych murali z lat sześćdziesiątych i
                siedemdziesiątych, a zaprojekto­wana kompozycja nawiązuje do
                dynamicznych kształtów budujących wyimaginowane światy i
                przestrzenie.
              </p>
              <p>
                Efekt ruchu zostanie uzyskany poprzez użycie wyłącznie białych
                kafli Ceramiki Paradyż o zróżnicowanych grubościach i fakturze
                powierzchni.
              </p>
            </ItemText>
          </Item>
          <Item odd>
            <ItemImage>
              <Img
                fluid={data.kreslona.edges[0].node.childImageSharp.fluid}
                alt="Modernizacja kreślona"
              />
            </ItemImage>
            <ItemText>
              <h2>
                <span>Modernizacja kreślona</span>
              </h2>
              <h3>
                Seikon
                <br />
                ul. Mściwoja 9
              </h3>
              <p>
                Charakterystyczne dla gdyńskiego streetartowca se­kwencje linii
                zostaną wpisane w podziały fasady. Styl Seikona zestawimy ze
                zmodyfikowaną techniką sgraffito, czyli rysunku w tynku. Praca
                zostanie podświetlona, co wraz z mienieniem się, typowym dla
                zastosowanego materiału, da niezwykły efekt wizualny.
              </p>
              <p>
                Projekt artystyczny stanie się częścią całościowej modernizacji
                budynku zawierającej elementy cyfrowe i interaktywne.
              </p>
            </ItemText>
          </Item>
          <Item>
            <ItemImage>
              <Img
                fluid={data.szyldowa.edges[0].node.childImageSharp.fluid}
                alt="Modernizacja szyldowa"
              />
            </ItemImage>
            <ItemText>
              <h2>
                <span>Modernizacja szyldowa</span>
              </h2>
              <h3>
                SYFON STUDIO
                <br />
                Bar mleczny Słoneczny ul. Abrahama 60
              </h3>
              <p>
                Pożegnajcie słoneczko w czapce marynarza. Kultowy „mleczak”
                zyska całkiem nową zewnętrzną identyfikację wizualną, dzięki
                duetowi Uli i Filipa Tofilów. Nie spo­dziewajcie się jednak
                rozsądnych i wyważonych ruchów. Studio Syfon nie boi się wyzwań
                i odważnych decyzji projektowych. Oprócz innych elementów
                identyfikują­cych lokal, Słoneczny rozbłyśnie również nowym
                neonem.
              </p>
              <p>
                O tym, jak powstawał projekt identyfikacji, współgra­jący z, jak
                mówią Tofile, tradycją tworzenia oznaczeń w tego typu
                przybytkach uciech kulinarnych, dowiecie się podczas spotkania z
                projektantami połączonego z wyjątkowym menu degustacyjnym.
              </p>
            </ItemText>
          </Item>
          <Item odd>
            <ItemImage>
              <Img
                fluid={data.duchowa.edges[0].node.childImageSharp.fluid}
                alt="Modernizacja duchowa"
              />
            </ItemImage>
            <ItemText>
              <h2>
                <span>Modernizacja duchowa</span>
              </h2>
              <h3>
                Vera King × Mikołaj Sałek
                <br />
                Różne lokalizacje
              </h3>
              <p>
                Zwracacie czasem uwagę na widoczne na ścianach bu­dynków kratki
                wentylacyjne? Vera King i Mikołaj Sałek uznali, że mogą one
                identyfikować miejskie duchy. W tym roku zobaczycie nie tylko
                niezwykłe połączenie ilustra­cji i metaloplastyki w użytkowej
                funkcji wentylacji, ale także ducha, a właściwie całą ich
                gromadę: Gwizducha, Podszeptucha, Wielkoducha…
              </p>
              <p>
                Sprawdźcie, jak dobrze znacie te miejskie stwory, biorąc udział
                w poświęconej kratkom grze miejskiej.
              </p>
            </ItemText>
          </Item>
          <Item>
            <ItemImage>
              <Img
                fluid={data.swiatla.edges[0].node.childImageSharp.fluid}
                alt="Modernizacja światła"
              />
            </ItemImage>
            <ItemText>
              <h2>
                <span>Modernizacja światła</span>
              </h2>
              <h3>
                Jacek Wielebski
                <br />
                ul. Mściwoja i ul. Zgody
              </h3>
              <p>
                Lampy to jeden z elementów, który nazywamy miejską biżuterią. Po
                zmroku dają światło, ale również za dnia mogą ożywiać
                przestrzeń, budować atmosferę miejsca i identyfikować budynek.
                Lampy przy wejściach do klatek schodowych są również często
                nośnikiem informacji o nazwie lokalu, ulicy, numerze domu.
                Zobaczcie projekt, który rozświetli drogę do kilku miejskich
                lokalizacji.
              </p>
            </ItemText>
          </Item>
          <Item odd>
            <ItemImage>
              <Img
                fluid={data.fasadowa.edges[0].node.childImageSharp.fluid}
                alt="Modernizacja fasadowa"
              />
            </ItemImage>
            <ItemText>
              <h2>
                <span>Modernizacja fasadowa</span>
              </h2>
              <h3>
                Jacek Rudzki
                <br />
                ul. Świętego Wojciecha 9
              </h3>
              <p>
                Projekt realizowany przez Znajomego Grafika wpisuje się w naszą
                filozofię wydobywania budynków z opresji paste­lowych fasad i
                zwariowanych kształtów. Rudzki stworzy mural, który przez
                zastosowanie dyskretnych wzorów graficznych podkreśli podziały
                bryły, jednocześnie wy­gaszając jej kolorystykę.
              </p>
              <p>
                Jednym z ważnych aspektów, który rozważamy podczas naszej pracy,
                jest potrzeba indywidualizmu wśród miesz­kańców i to jak
                pogodzić ją z pragnieniem estetycznej spójności. Projekt
                Rudzkiego, wprowadzając minimali­ styczne wzory i symbole,
                pokazuje, jak można dążyć do dobrego kompromisu.
              </p>
            </ItemText>
          </Item>
        </Layout>
      </Fragment>
    );
  }
}

PLBiennale2018Works.propTypes = {
  data: shape({
    duchowa: object.isRequired
  }).isRequired,
  location: object.isRequired
};

export const pageQuery = graphql`
  query PlBiennale2018WorksQuery {
    duchowa: allFile(
      filter: { relativePath: { eq: "realizacje/duchowa.png" } }
    ) {
      edges {
        node {
          childImageSharp {
            fluid(
              maxWidth: 600
              maxHeight: 800
              quality: 90
              cropFocus: EAST
              traceSVG: { color: "lightgray" }
            ) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    }
    falowa: allFile(filter: { relativePath: { eq: "realizacje/falowa.png" } }) {
      edges {
        node {
          childImageSharp {
            fluid(
              maxWidth: 600
              maxHeight: 800
              quality: 90
              cropFocus: EAST
              traceSVG: { color: "lightgray" }
            ) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    }
    fasadowa: allFile(
      filter: { relativePath: { eq: "realizacje/fasadowa.png" } }
    ) {
      edges {
        node {
          childImageSharp {
            fluid(
              maxWidth: 600
              maxHeight: 800
              quality: 90
              cropFocus: CENTER
              traceSVG: { color: "lightgray" }
            ) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    }
    jazni: allFile(filter: { relativePath: { eq: "realizacje/jazni.png" } }) {
      edges {
        node {
          childImageSharp {
            fluid(
              maxWidth: 600
              maxHeight: 800
              quality: 90
              cropFocus: CENTER
              traceSVG: { color: "lightgray" }
            ) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    }
    kreslona: allFile(
      filter: { relativePath: { eq: "realizacje/kreslona.png" } }
    ) {
      edges {
        node {
          childImageSharp {
            fluid(
              maxWidth: 600
              maxHeight: 800
              quality: 90
              cropFocus: EAST
              traceSVG: { color: "lightgray" }
            ) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    }
    swiatla: allFile(
      filter: { relativePath: { eq: "realizacje/swiatla.png" } }
    ) {
      edges {
        node {
          childImageSharp {
            fluid(
              maxWidth: 600
              maxHeight: 800
              quality: 90
              cropFocus: EAST
              traceSVG: { color: "lightgray" }
            ) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    }
    szyldowa: allFile(
      filter: { relativePath: { eq: "realizacje/szyldowa.png" } }
    ) {
      edges {
        node {
          childImageSharp {
            fluid(
              maxWidth: 600
              maxHeight: 800
              quality: 90
              cropFocus: EAST
              traceSVG: { color: "lightgray" }
            ) {
              ...GatsbyImageSharpFluid_withWebp_tracedSVG
            }
          }
        }
      }
    }
  }
`;
